<template>
  <my-wrapper title="">
    <template v-slot:extra>
      
    </template>
    <my-panel theme="border-top" title="" size="large">
      <my-form label-width="100px"
               :model="form"
               :rules="rules"
               @submit="handleSubmit">
        <my-input label="" name="title" placeholder=""></my-input>
        <my-date-picker label="" name="range" :props="{
          type: 'daterange',
          startPlaceholder: '',
          endPlaceholder: ''
        }"></my-date-picker>
        <my-input label="" name="info" :props="{type:'textarea',rows:5}"></my-input>
        <my-input label="" name="standard" :props="{type:'textarea'}"></my-input>
        <my-select label="" name="customer" :props="{multiple:true}" :loader="loader"></my-select>
        <my-select label="" name="commenter" :loader="loader"></my-select>
        <my-input-number label="" name="weight" width="300px"
                         :props="{controlsPosition:'right',min:1, max:10}"></my-input-number>
        <my-radio label="" name="opening" dict="option" :loader="loader">
        </my-radio>
      </my-form>
    </my-panel>
  </my-wrapper>
</template>

<script>
  
     import __vue__ from 'vue';
     
     import {MyDatePicker as __MyDatePicker__,MyForm as __MyForm__,MyInput as __MyInput__,MyInputNumber as __MyInputNumber__,MyPanel as __MyPanel__,MyRadio as __MyRadio__,MySelect as __MySelect__,MyWrapper as __MyWrapper__} from '$ui';
     
     
     
     export default {
    data() {
      return {
        form: {
          opening: 1
        },
        rules: {
          title: {required: true, message: ''}
        }
      }
    },
    methods: {
      loader(model, {name, dict}) {
        const options = [
          {label: '', value: 1},
          {label: '', value: 2},
          {label: '', value: 3}
        ]
        return Promise.resolve(options)
      },
      handleSubmit(model) {
        this.$message.success('')
      }
    }
  }
     
     __vue__.use(__MyDatePicker__);
__vue__.use(__MyForm__);
__vue__.use(__MyInput__);
__vue__.use(__MyInputNumber__);
__vue__.use(__MyPanel__);
__vue__.use(__MyRadio__);
__vue__.use(__MySelect__);
__vue__.use(__MyWrapper__)
    
  </script>

